import React, { useEffect, useState } from "react";
import moment from "moment";
import { useCurrentLocation } from "../hooks/useCurrentLocation ";
import "../styles/region-electricity.css";
import "../styles/typography.css";
import { CarbonIntensityBar } from "./CarbonIntensityBar";
import { Colors } from '../types';

interface CarbonIntensity {
    carbonIntensity: number;
    datetime?: Date;
}

interface PowerConsumptionBreakdown {
    fossilFreePercentage?: number;
    powerConsumptionBreakdown?: {
        biomass: number;
        coal: number;
        gas: number;
        geothermal: number;
        hydro: number;
        hydroDischarge: number;
        nuclear: number;
        oil: number;
        solar: number;
        unknown: number;
        wind: number;
    };
    powerConsumptionTotal?: number;
    renewablePercentage: number;
    datetime?: Date;
}

const electricityMapAPIToken = process.env.REACT_APP_ELECTRICITY_MAP_API_TOKEN!;
const headers = new Headers();
headers.append("auth-token", electricityMapAPIToken);

const colors: Colors = {
    0: 'rgba(68,245,8,1)',
    200: 'rgba(255,239,0,1)',
    400: 'rgba(212,122,0,1)',
    600: 'rgba(126,100,3, 1)',
    800: 'rgba(50,0,80,1)'
}

export function RegionElectricity() {
    const { latitude, longitude, error } = useCurrentLocation();
    const [
        carbonIntensity,
        setCarbonIntensity,
    ] = useState<CarbonIntensity | null>(null);
    const [
        powerConsumptionBreakdown,
        setPowerConsumptionBreakdown,
    ] = useState<PowerConsumptionBreakdown | null>(null);

    useEffect(() => {
        fetch(
            `https://api.electricitymap.org/v3/carbon-intensity/latest?lat${latitude}&lon=${longitude}`,
            {
                headers: headers,
            }
        )
            .then((response) => response.json())
            .then((data) => setCarbonIntensity(data));

        fetch(
            `https://api.electricitymap.org/v3/power-consumption-breakdown/latest?lat${latitude}&lon=${longitude}`,
            {
                headers: headers,
            }
        )
            .then((response) => response.json())
            .then((result) => setPowerConsumptionBreakdown(result));
    }, [latitude, longitude, carbonIntensity?.datetime]);


    let backgroundColor = 'white';
    if (carbonIntensity && carbonIntensity?.carbonIntensity < 200) {
        backgroundColor = colors['0'];
    } else if (carbonIntensity && carbonIntensity?.carbonIntensity < 400) {
        backgroundColor = colors['200'];
    } else if (carbonIntensity && carbonIntensity?.carbonIntensity < 600) {
        backgroundColor = colors['400'];
    } else if (carbonIntensity && carbonIntensity?.carbonIntensity < 800) {
        backgroundColor = colors['600'];
    } else {
        backgroundColor = colors['800'];
    }

    return (
        <>
            {error ?
                <div>
                    <div>Unfortunately you can not see the information about the electricity :( .</div>
                    <div> To see this information you need to allow access to your location ;)</div>
                </div>
                :
                <div>
                    <h2 className="text-xl">Electricity information at your location</h2>
                    {carbonIntensity && (
                        <div className="date text-sm">
                            {" "}
                            at{" "}
                            {moment(carbonIntensity.datetime).format("MMMM Do YYYY, h:mm:ss a")}
                        </div>
                    )}
                    <div className="flex-row text-md">
                        {carbonIntensity && (
                            <div className="box-wrapper">
                                <div className="box big-box" style={{ backgroundColor: backgroundColor }}>
                                    <div className="text-sm">{carbonIntensity.carbonIntensity}</div>
                                    <div className="text-xs"> gCO₂eq/ kWh</div>
                                </div>
                                <div>carbon intensity</div>
                            </div>
                        )}
                        {powerConsumptionBreakdown && (
                            <div className="box-wrapper">
                                <div className="box small-box text-sm">
                                    {powerConsumptionBreakdown.renewablePercentage}%
            </div>
                                <div>renewable</div>
                            </div>
                        )}
                        {powerConsumptionBreakdown && (
                            <div className="box-wrapper">
                                <div className="box small-box text-sm">
                                    {powerConsumptionBreakdown.fossilFreePercentage}%
            </div>
                                <div>fossil free</div>
                            </div>
                        )}
                    </div>

                    <CarbonIntensityBar colors={colors} />

                    {powerConsumptionBreakdown && (
                        <div className="text-md">
                            Electricity consumption: <br />
                            {powerConsumptionBreakdown.powerConsumptionBreakdown && (
                                <div>
                                    <table>
                                        <tbody>
                                            <tr>
                                                <td>biomass</td>
                                                <td className="bar">
                                                    <div
                                                        className="color-bar biomass"
                                                        style={{
                                                            width:
                                                                (powerConsumptionBreakdown.powerConsumptionBreakdown
                                                                    .biomass *
                                                                    100) /
                                                                1000 +
                                                                "%",
                                                        }}
                                                    ></div>
                                                    {powerConsumptionBreakdown.powerConsumptionTotal && (
                                                        <div className="popup">
                                                            {Math.round(
                                                                (powerConsumptionBreakdown.powerConsumptionBreakdown
                                                                    .biomass *
                                                                    100) /
                                                                powerConsumptionBreakdown.powerConsumptionTotal
                                                            ) +
                                                                "% of electricity in your region comes from biomass"}
                                                        </div>
                                                    )}
                                                </td>
                                            </tr>
                                            <tr>
                                                <td>coal</td>
                                                <td className="bar">
                                                    <div
                                                        className="color-bar coal"
                                                        style={{
                                                            width:
                                                                (powerConsumptionBreakdown.powerConsumptionBreakdown
                                                                    .coal *
                                                                    100) /
                                                                1000 +
                                                                "%",
                                                        }}
                                                    ></div>
                                                    {powerConsumptionBreakdown.powerConsumptionTotal && (
                                                        <div className="popup">
                                                            {Math.round(
                                                                (powerConsumptionBreakdown.powerConsumptionBreakdown
                                                                    .coal *
                                                                    100) /
                                                                powerConsumptionBreakdown.powerConsumptionTotal
                                                            ) + "% of electricity in your region comes from coal"}
                                                        </div>
                                                    )}
                                                </td>
                                            </tr>
                                            <tr>
                                                <td>gas</td>
                                                <td className="bar">
                                                    <div
                                                        className="color-bar gas"
                                                        style={{
                                                            width:
                                                                (powerConsumptionBreakdown.powerConsumptionBreakdown
                                                                    .gas *
                                                                    100) /
                                                                1000 +
                                                                "%",
                                                        }}
                                                    ></div>
                                                    {powerConsumptionBreakdown.powerConsumptionTotal && (
                                                        <div className="popup">
                                                            {Math.round(
                                                                (powerConsumptionBreakdown.powerConsumptionBreakdown
                                                                    .gas *
                                                                    100) /
                                                                powerConsumptionBreakdown.powerConsumptionTotal
                                                            ) + "% of electricity in your region comes from gas"}
                                                        </div>
                                                    )}
                                                </td>
                                            </tr>
                                            <tr>
                                                <td>geothermal</td>
                                                <td className="bar">
                                                    <div
                                                        className="color-bar geothermal"
                                                        style={{
                                                            width:
                                                                (powerConsumptionBreakdown.powerConsumptionBreakdown
                                                                    .geothermal *
                                                                    100) /
                                                                1000 +
                                                                "%",
                                                        }}
                                                    ></div>
                                                    {powerConsumptionBreakdown.powerConsumptionTotal && (
                                                        <div className="popup">
                                                            {Math.round(
                                                                (powerConsumptionBreakdown.powerConsumptionBreakdown
                                                                    .geothermal *
                                                                    100) /
                                                                powerConsumptionBreakdown.powerConsumptionTotal
                                                            ) +
                                                                "% of electricity in your region comes from geothermal"}
                                                        </div>
                                                    )}
                                                </td>
                                            </tr>
                                            <tr>
                                                <td>hydro</td>
                                                <td className="bar">
                                                    <div
                                                        className="color-bar hydro"
                                                        style={{
                                                            width:
                                                                (powerConsumptionBreakdown.powerConsumptionBreakdown
                                                                    .hydro *
                                                                    100) /
                                                                1000 +
                                                                "%",
                                                        }}
                                                    ></div>
                                                    {powerConsumptionBreakdown.powerConsumptionTotal && (
                                                        <div className="popup">
                                                            {Math.round(
                                                                (powerConsumptionBreakdown.powerConsumptionBreakdown
                                                                    .hydro *
                                                                    100) /
                                                                powerConsumptionBreakdown.powerConsumptionTotal
                                                            ) +
                                                                "% of electricity in your region comes from hydro"}
                                                        </div>
                                                    )}
                                                </td>
                                            </tr>
                                            <tr>
                                                <td>nuclear</td>
                                                <td className="bar">
                                                    <div
                                                        className="color-bar nuclear"
                                                        style={{
                                                            width:
                                                                (powerConsumptionBreakdown.powerConsumptionBreakdown
                                                                    .nuclear *
                                                                    100) /
                                                                1000 +
                                                                "%",
                                                        }}
                                                    ></div>
                                                    {powerConsumptionBreakdown.powerConsumptionTotal && (
                                                        <div className="popup">
                                                            {Math.round(
                                                                (powerConsumptionBreakdown.powerConsumptionBreakdown
                                                                    .nuclear *
                                                                    100) /
                                                                powerConsumptionBreakdown.powerConsumptionTotal
                                                            ) +
                                                                "% of electricity in your region comes from nuclear"}
                                                        </div>
                                                    )}
                                                </td>
                                            </tr>
                                            <tr>
                                                <td>oil</td>
                                                <td className="bar">
                                                    <div
                                                        className="color-bar oil"
                                                        style={{
                                                            width:
                                                                (powerConsumptionBreakdown.powerConsumptionBreakdown
                                                                    .oil *
                                                                    100) /
                                                                1000 +
                                                                "%",
                                                        }}
                                                    ></div>
                                                    {powerConsumptionBreakdown.powerConsumptionTotal && (
                                                        <div className="popup">
                                                            {Math.round(
                                                                (powerConsumptionBreakdown.powerConsumptionBreakdown
                                                                    .oil *
                                                                    100) /
                                                                powerConsumptionBreakdown.powerConsumptionTotal
                                                            ) + "% of electricity in your region comes from oil"}
                                                        </div>
                                                    )}
                                                </td>
                                            </tr>
                                            <tr>
                                                <td>solar</td>
                                                <td className="bar">
                                                    <div
                                                        className="color-bar solar"
                                                        style={{
                                                            width:
                                                                (powerConsumptionBreakdown.powerConsumptionBreakdown
                                                                    .solar *
                                                                    100) /
                                                                1000 +
                                                                "%",
                                                        }}
                                                    ></div>
                                                    {powerConsumptionBreakdown.powerConsumptionTotal && (
                                                        <div className="popup">
                                                            {Math.round(
                                                                (powerConsumptionBreakdown.powerConsumptionBreakdown
                                                                    .solar *
                                                                    100) /
                                                                powerConsumptionBreakdown.powerConsumptionTotal
                                                            ) +
                                                                "% of electricity in your region comes from solar"}
                                                        </div>
                                                    )}
                                                </td>
                                            </tr>
                                            <tr>
                                                <td>unknown</td>
                                                <td className="bar">
                                                    <div
                                                        className="color-bar unknown"
                                                        style={{
                                                            width:
                                                                (powerConsumptionBreakdown.powerConsumptionBreakdown
                                                                    .unknown *
                                                                    100) /
                                                                1000 +
                                                                "%",
                                                        }}
                                                    ></div>
                                                    {powerConsumptionBreakdown.powerConsumptionTotal && (
                                                        <div className="popup">
                                                            {Math.round(
                                                                (powerConsumptionBreakdown.powerConsumptionBreakdown
                                                                    .unknown *
                                                                    100) /
                                                                powerConsumptionBreakdown.powerConsumptionTotal
                                                            ) +
                                                                "% of electricity in your region comes from unknown"}
                                                        </div>
                                                    )}
                                                </td>
                                            </tr>
                                            <tr>
                                                <td>wind</td>
                                                <td className="bar">
                                                    <div
                                                        className="color-bar wind"
                                                        style={{
                                                            width:
                                                                (powerConsumptionBreakdown.powerConsumptionBreakdown
                                                                    .wind *
                                                                    100) /
                                                                1000 +
                                                                "%",
                                                        }}
                                                    ></div>
                                                    {powerConsumptionBreakdown.powerConsumptionTotal && (
                                                        <div className="popup">
                                                            {Math.round(
                                                                (powerConsumptionBreakdown.powerConsumptionBreakdown
                                                                    .wind *
                                                                    100) /
                                                                powerConsumptionBreakdown.powerConsumptionTotal
                                                            ) + "% of electricity in your region comes from wind"}
                                                        </div>
                                                    )}
                                                </td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                            )}
                        </div>
                    )}
                </div>}
        </>
    );
}
