import * as React from 'react';
import "../styles/typography.css";
import "../styles/carbon-intensity.css";
import { Colors } from '../types';

interface IProps {
  colors: Colors
}

export function CarbonIntensityBar(props: IProps) {
  const gradient = `linear-gradient(90deg,  ${props.colors['0']} 0%, ${props.colors['200']}  20%, ${props.colors['400']}  45%, ${props.colors['600']}  70%, ${props.colors['800']}  100%)`
  return (
    <div className="text-sm">
      <div>Carbon intencity(gCO₂eq/kWh)</div>
      <div className="carbon-bar" style={{ background: gradient }}>
        <div className="bar-0">0</div>
        <div className="bar-200">200</div>
        <div className="bar-400">400</div>
        <div className="bar-600">600</div>
        <div className="bar-800">800</div>
      </div>
    </div>
  );
};